                    { label: "Odcinki", key: "points_stretch" },
                    { label: "amana", key: "points_path" },
                ],
                array: [
                    { label: "Wyrnienie komrek", key: "array_index" },
                    { label: "Przedzia", key: "array_part" },
                ],
            },
        };
    },

    methods: {
        handleSelectOption(selectedOption) {
            this.$props.callback(selectedOption);
